





































import { LevelData } from '@/models';
import { Component, Vue, Watch } from 'vue-property-decorator';
import { Action, Getter } from 'vuex-class';
import FlipCard from '../components/FlipCard.vue';
import VictoryModal from '../components/VictoryModal.vue';

@Component({
  name: 'Play',
  components: {
    VictoryModal,
    FlipCard,
  },
})
export default class Play extends Vue {
  @Getter('level')
  public sourceData!: LevelData;

  @Action('replay')
  public replayGame!: () => Promise<void>;

  @Action('save')
  public saveGame!: () => void;

  private lastFlipIndex = -1;
  private count = 0;
  // local cached data
  public data: LevelData | null = null;

  public cards: { name: string; up: boolean }[] = [];

  public configXs = {
    back: { size: 40 },
    card: { 0: 120, 1: 120, 2: 120, 3: 90, 4: 70, 5: 70 },
  };
  public configSm = {
    back: { size: 50 },
    card: { 0: 140, 1: 140, 2: 140, 3: 100, 4: 80, 5: 80 },
  };
  public configMd = {
    back: { size: 80 },
    card: { 0: 260, 1: 260, 2: 220, 3: 200, 4: 160, 5: 160 },
  };
  public config = this.configMd;
  private yay: HTMLAudioElement | null = null;

  private resetGame() {
    this.data = { ...this.sourceData };
    this.cards = this.sourceData.cards.map(c => ({ name: c, up: false }));
    this.count = 0;
    this.lastFlipIndex = -1;
  }

  public created() {
    this.resetGame();
  }

  public mounted() {
    this.resolveConfig();
    this.yay = new Audio(require(`../assets/sounds/yay.mp3`));
  }

  private resolveConfig() {
    if (this.$vuetify.breakpoint.xsOnly) {
      this.config = this.configXs;
    } else if (this.$vuetify.breakpoint.smOnly) {
      this.config = this.configSm;
    } else if (this.$vuetify.breakpoint.mdAndUp) {
      this.config = this.configMd;
    }
  }

  public get victory() {
    return this.count && this.count === this.sourceData.cards.length;
  }

  @Watch('victory')
  public onVictory() {
    if (this.victory) {
      this.saveGame();
    }
  }

  public async replay() {
    await this.replayGame();
    this.resetGame();
  }

  public flip(index: number) {
    if (index === this.lastFlipIndex) {
      return;
    }
    if (this.cards[index].up) {
      return;
    }
    if (this.cards[this.lastFlipIndex]) {
      if (this.cards[this.lastFlipIndex].name === this.cards[index].name) {
        this.cards[index].up = true;
        this.lastFlipIndex = -1;
        if (this.yay) {
          this.yay.play();
        }
        this.count += 2;
        this.$emit('score');
        return;
      } else {
        this.cards[this.lastFlipIndex].up = false;
        this.cards[index].up = true;
      }
    } else {
      this.cards[index].up = true;
    }
    this.lastFlipIndex = index;
  }
}
