








































import Vue from 'vue';
import { Component, Model, Prop, Watch } from 'vue-property-decorator';
import StarBadge from './StarBadge.vue';

@Component({
  name: 'VictoryModal',
  components: {
    StarBadge,
  },
})
export default class VictoryModal extends Vue {
  @Model('input')
  public show!: boolean;

  public localShow = false;
  public configXs = {
    width: 300,
    badge: {
      size: 100,
    },
    home: {
      size: 50,
      icon: {
        size: 40,
      },
    },
    play: {
      size: 50,
      icon: {
        size: 40,
      },
    },
  };
  public configSm = {
    width: 400,
    badge: {
      size: 180,
    },
    home: {
      size: 80,
      icon: {
        size: 60,
      },
    },
    play: {
      size: 80,
      icon: {
        size: 60,
      },
    },
  };
  public configMd = {
    width: 800,
    badge: {
      size: 350,
    },
    home: {
      size: 150,
      icon: {
        size: 110,
      },
    },
    play: {
      size: 150,
      icon: {
        size: 110,
      },
    },
  };
  public config = this.configMd;

  @Watch('show', { immediate: true })
  public onShowChanged() {
    if (this.show != this.localShow) {
      this.localShow = this.show;
    }
  }

  @Watch('localShow', { immediate: true })
  public onLocalShowChanged() {
    if (this.show != this.localShow) {
      this.$emit('input', this.localShow);
    }
  }

  public mounted() {
    this.resolveConfig();
  }

  private resolveConfig() {
    if (this.$vuetify.breakpoint.smOnly) {
      this.config = this.configSm;
    } else if (this.$vuetify.breakpoint.mdAndUp) {
      this.config = this.configMd;
    } else if (this.$vuetify.breakpoint.xsOnly) {
      this.config = this.configXs;
    }
  }
}
